import React, { useState } from 'react'
import './App.css' 
function App() {
  const [name,setName]=useState(0);
  function clickMe() {
    setName(name+1);    
  }
  return (
    <div className="wrapper">
      <p>{name}</p>
      <button onClick={clickMe}>Click me</button>
    </div>
  )
}

export default App